@import url('https://use.typekit.net/ppg0eht.css');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;600&display=swap');

#root {
	max-width: 100vw;
}

* {
	scroll-behavior: smooth;
}

body {
	background-color: rgb(255, 250, 250);
	margin: 0;
	padding: 0;
	max-width: 100vw;
}

.app {
	height: fit-content;
	display: flex;
	flex-direction: column;
	max-width: 100vw;
}

section {
	flex-basis: 100vh;
	border-style: solid;
	border-width: 0;
	border-color: #88bcd8;
	border-bottom-width: 3px;
	padding-top: 1.5rem;
	max-width: 100vw;
}

.centered {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.section-header {
	margin-bottom: 0rem;
}

section .anchor {
	display: block;
	position: relative;
	top: -6rem;
	visibility: hidden;
}

.landing-page {
	display: flex;
	flex-direction: column-reverse;
	align-items: center;
	width: 100vw;
	justify-content: center;
	position: relative;
	overflow: hidden;
	padding: 0;
}

.landing-page .background {
	display: none;
}

.landing-page .svg {
	width: 100%;
	height: 100%;
}

.landing-page .background path {
	stroke-width: 20px;
}

.arrow-down {
	display: none;
}

@keyframes stroke-grow {
	from {
		stroke-width: 40px;
	}
	to {
		stroke-width: 0;
	}
}

@keyframes slide {
	from {
		opacity: 0;
		transform: translateX(-20vw);
	}
	to {
		opacity: 1;
		transform: translateX(0);
	}
}

.landing-page .flex-spacer {
	display: none;
	flex-basis: 6vw;
	flex-grow: 0;
	flex-shrink: 0.2;
}

.landing-page .portrait {
	min-width: 50vw;
	flex-basis: 20vh;
	flex-grow: 1;
	flex-shrink: 1;
	display: flex;
	align-items: center;
	justify-content: center;
}

.landing-page .portrait svg {
	width: 80%;
}

@keyframes dash {
	to {
		stroke-dashoffset: 0;
	}
}

@keyframes fade {
	to {
		opacity: 1;
	}
}

.landing-page .content {
	width: 100%;
	flex-basis: auto;
	flex-grow: 1;
	flex-shrink: 1;
	background: repeating-linear-gradient(to bottom, #a4def4, #a4def4 0.8rem, #95d8ed 0.8rem, #95d8ed 1.6rem);
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.landing-page .content h1 {
	padding: 0 2rem;
	color: #756c98;
	white-space: nowrap;
	font-size: max(30px, 5vmax);
	font-family: montserrat, sans-serif;
	font-weight: 800;
	font-style: normal;
	margin-block-start: 0;
	margin-block-end: 1rem;
	clip-path: inset(100% 100% 100% 100%);
	animation: reveal-left-to-right 0.4s 1.2s ease-in forwards;
}

@keyframes reveal-left-to-right {
	from {
		white-space: normal;
		clip-path: inset(0% 100% 0% 0%);
	}
	to {
		white-space: normal;
		clip-path: inset(0% 0% 0% 0%);
	}
}

@keyframes reveal-up-to-down {
	from {
		white-space: normal;
		clip-path: inset(0% 0% 100% 0%);
	}
	to {
		white-space: normal;
		clip-path: inset(0% 0% 0% 0%);
	}
}

.landing-page .content h1 strong {
	color: #4189c4;
	font-family: montserrat, sans-serif;
	font-weight: 800;
	font-style: normal;
}

.landing-page .code-section {
	display: block;
	padding: 0 2rem;
}

.landing-page code {
	display: inline-block;
	padding-left: 0.5rem;
	font-family: 'Source Code Pro', monospace;
	font-weight: 600;
	font-size: max(16px, 1.5vmax);
	color: #756c98;
	clip-path: inset(100% 100% 100% 100%);
	animation: reveal-up-to-down 0.4s 1.8s ease-in forwards;
}

.landing-page .line-numbers {
	width: fit-content;
	color: #4189c4;
	padding: 0.5rem;
	border-style: solid;
	border-width: 0;
	border-right-width: 4px;
	border-color: #4189c4;
}

.landing-page code p {
	margin: 0 0.5rem;
}

.landing-page code .keyword {
	color: #25679e;
}
.landing-page code .variable {
	color: #4189c4;
}
.landing-page code .square-bracket {
	color: #b77682;
}
.landing-page code .punctuation {
	color: #afa8ca;
}
.landing-page code .string {
	padding-left: 4rem;
	color: #756c98;
}

.landing-page menu {
	padding: 0 2rem;
	display: flex;
	flex-direction: column;
	gap: 1rem;
	clip-path: inset(100% 100% 100% 100%);
	animation: reveal-up-to-down 0.4s 2.1s ease-in forwards;
}

.landing-page a {
	color: #4189c4;
	background-color: white;
	padding: 0.5rem 2rem;
	text-transform: uppercase;
	font-family: montserrat, sans-serif;
	text-align: center;
	font-weight: 400;
	font-size: max(12px, 1.5vmax);
	text-decoration: none;
	border-color: none;
	border-width: 0;
	border-radius: 0.5rem;
}

nav {
	height: 4rem;
	width: 100%;
	position: sticky;
	top: 0;
	background-color: rgb(255, 250, 250);
	text-decoration: none;
	border-color: #25679e;
	border-width: 0;
	border-bottom-width: 1px;
	border-style: solid;
	display: none;
}

nav menu {
	flex-grow: 1;
	flex-basis: max(800px, 80vw);
	margin: 0;
	padding-inline-start: 0;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	justify-items: center;
}

nav li {
	display: flex;
	align-items: center;
	list-style: none;
	margin: 1rem 0;
	justify-content: center;
}

nav a {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: 'Source Code Pro', monospace;

	color: #4189c4;
	text-decoration: none;
	font-size: 2rem;
	text-transform: lowercase;
	transition-property: background-color;
	transition-duration: 0.1s;
}

nav .number {
	display: none;
	list-style: none;
	font-size: 1.5rem;
	font-family: 'Source Code Pro', monospace;
	color: #4189c4;
	padding: 0 0.5rem;
	margin-right: 1rem;
	border-style: solid;
	border-width: 0;
	border-right-width: 1px;
}

nav .active::before {
	position: absolute;
	content: '';
	background: #a4def4;
	width: 120%;
	height: 30%;
	top: 60%;
	left: -10%;
	z-index: -1;
	border-radius: 3rem;
}

.nav-margin {
	flex-basis: max(2rem, 10vw);
	flex-grow: 1;
}

#skills-section {
	display: flex;
	flex-direction: column;
}

#skills-section .section-header {
	flex-grow: 0;
}

#skills-section article {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	align-self: center;
	justify-self: center;
	align-items: center;
	justify-content: center;
}

#skills-section article .flex-content {
	padding-bottom: 10rem;
	display: flex;
	flex-direction: row;
	gap: 1.5rem;
	align-items: flex-start;
	justify-content: center;
	flex-wrap: wrap;
	flex-basis: 0.5;
	flex-grow: 0;
}

#projects-section article {
	display: flex;
	flex-direction: column;
	gap: 2rem;
	align-items: center;
	justify-content: space-evenly;
}

#projects-section article .project-info {
	flex-basis: max(20vw, 250px);
	font-size: clamp(20px, 1.8vw, 24px);
}

#projects-section .spacer {
	flex-basis: 10vw;
}

.flex-spacer {
	flex-grow: 1;
	flex-basis: 1;
	flex-shrink: 1;
}

.flex-content {
	padding: 0 1rem;
	display: flex;
	flex-direction: column;
	align-items: stretch;
	flex-basis: 0.5;
	flex-grow: 0;
}

#about-section article {
	margin-top: 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: auto;
	display: flex;
	justify-content: center;
}

#about-section article .spacer {
	flex-basis: 10vw;
}

#about-section .left {
	display: flex;
	flex-basis: auto;
	padding-top: 0rem;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 0;
}

#about-section .socials {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
}

#about-section .left .socials a {
	margin: 0.125rem;
	display: grid;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: 0.9rem;
	text-transform: lowercase;
	text-decoration: none;
}

#about-section .left .socials .svg-icon {
	width: 2.3rem;
}

#about-section .left .socials label {
	display: block;
	color: #4189c4;
}

#about-section .left figure {
	width: fit-content;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-block-end: 0;
	margin-block-start: 0;
	margin-inline-end: 0;
	margin-inline-start: 0;
	margin: 0.25rem;
}

#about-section .left figcaption {
	display: none;
}

#about-section .left .jpg {
	min-width: 200px;
	width: 30vw;
	border-style: solid;
	border-width: 1px;
	border-color: black;
}

#about-section .right h2 {
	max-width: 60ch;
	font-family: montserrat, sans-serif;
	font-weight: 700;
	margin-block-start: 2rem;
	color: #25679e;
	font-size: 1.2rem;
	padding: 0 2rem;
}

#about-section .right p {
	max-width: 60ch;
	padding: 0 2rem;
	font-family: montserrat, sans-serif;
	font-weight: 400;
	font-size: 1rem;
	line-height: 2.2ch;
}

#about-section .right .reference {
	color: #6d6c96;
	display: none;
}

#about-section .right strong {
	color: #b77682;
	font-weight: 600;
}

#about-section .bottom .icon-list .list-element {
	margin: 1.5rem 0;
}
#about-section .bottom .icon-list .title {
	display: inline;
	margin-left: 2rem;
	font-size: 24px;
}
#about-section .bottom .icon-list .text {
	display: inline;
	margin-right: 1rem;
	font-weight: 600;
	font-size: 18px;
}
#about-section .bottom .icon-list .subtext {
	display: inline;
	font-size: 18px;
}

#about-section .right-content .soft-skills {
	display: flex;
	gap: 3rem;
	justify-content: space-evenly;
}

#about-section .right-content .info-box {
	flex-basis: auto;
}

#about-section .right-content .soft-skills .spacer {
	flex-basis: 10%;
}

.blog-list {
	display: flex;
	flex-direction: column;
	align-items: center;
}

