.landing-page .portrait path.line {
	fill: none;
	stroke-linecap: round;
	stroke-linejoin: round;
	stroke-dasharray: 1500;
	stroke-dashoffset: 1500;
	animation: dash 1.5s ease-in-out forwards;
}
.landing-page .portrait path.shape {
	opacity: 0;
	animation: fade 0.5s linear forwards;
}

.landing-page .portrait path.nodelay {
	animation-delay: 0;
}
.landing-page .portrait path.first {
	animation-delay: 0.25s;
}
.landing-page .portrait path.second {
	animation-delay: 0.5s;
}
.landing-page .portrait path.third {
	animation-delay: 0.65s;
}
.landing-page .portrait path.forth {
	animation-delay: 0.85s;
}
.landing-page .portrait path.fifth {
	animation-delay: 1s;
}

.landing-page .portrait path.fast {
	animation-duration: 0.75s;
}
.landing-page .portrait path.slow {
	animation-duration: 2.5s;
}

.landing-page .portrait path.thin {
	stroke-width: 1px;
	stroke: #44406c;
}
.landing-page .portrait path.thick {
	stroke-width: 5px;
	stroke: #756c98;
}

.landing-page .portrait path.skin {
	fill: #eab2bb;
}

.landing-page .portrait path.shirt {
	fill: #88bcd8;
}

.landing-page .portrait path.hair {
	fill: #756c98;
}

.landing-page .portrait path.eyeshadow {
	fill: #badcee;
}

.landing-page .portrait path.mark {
	fill: red;
}
